import "./styles.css";

// initial todos
// DO NOT EDIT THIS ARRAY
// You may add props to objects if needed.
let todos = [
  {
    todoID: 0,
    todoText: "Finish Homework",
    todoComplete: false
  },
  {
    todoID: 1,
    todoText: "Walk the dog",
    todoComplete: true
  },
  {
    todoID: 2,
    todoText: "Clean my room",
    todoComplete: false
  }
];

const newTodoInput = document.querySelector(".todoInput");
const newTodoBtn = document.querySelector(".newTodoBtn");
const clearBtn = document.querySelector("#clearButton");
const listDiv = document.querySelector(".todoList");
listDiv.innerHTML = "";
const allLI = document.querySelectorAll(".todoList > li");

allLI.forEach((el) => {
  el.addEventListener("click", () => {
    if (el.className === "done") {
      el.className = "";
      return;
    }
    if (el.className === "") {
      el.className = "done";
      return;
    }
  });
});

const state = (() => {
  todos.forEach((el) => {
    console.log(el);

    const li = document.createElement("li");
    li.textContent = el.todoText;
    if (el.todoComplete) li.className = "done";

    const span = document.createElement("span");
    const i = document.createElement("i");
    i.className = "fa fa-trash";
    span.appendChild(i);

    li.addEventListener("click", () => {
      console.log("hello");
      if (li.className === "") {
        li.className = "done";
        el.todoComplete = true;
        return;
      } else {
        li.className = "";
        el.todoComplete = false;
      }
    });

    li.appendChild(span);
    listDiv.appendChild(li);
  });
})();

const newTodoHandler = (todoText) => {
  const newID = todos.length;
  const newTodoObject = {
    todoID: newID,
    todoText: todoText,
    todoComplete: false
  };

  return newTodoObject;
};

const newItemHandler = (todoText) => {
  console.log(listDiv);
  // listDiv.innerHTML = ""
  const newLI = document.createElement("li");
  newLI.textContent = todoText;
  newLI.className = "";
  listDiv.appendChild(newLI);
};

newTodoBtn.addEventListener("click", () => {
  const todoTitle = newTodoInput.value;
  const res = newTodoHandler(todoTitle);
  if (todoTitle === "") {
    console.log("todo text empty");
    return;
  }
  todos.push(res);
  newItemHandler(todoTitle);
});

newTodoInput.addEventListener("keypress", (event) => {
  if (event.key === "Enter") {
    const res = newTodoHandler(newTodoInput.value);
    todos.push(res);
    newItemHandler(newTodoInput.value);
  } else return;
});

clearBtn.addEventListener("click", () => {
  // todos.length = "";
});
